import React, { useState, useRef, useEffect } from "react";
import { register } from "../../commons/ComponentRegistry";
import { EventAPI } from "../../commons/EventAPI";
import wrapper from "../../commons/WebComponentWrapper";

const dispatchedEvents = [];

/*
    Example: 
        <srs-form sections=[{name: "section1", title: "Section 1"}, {name: "section2", title: "Section 2"}]>
            <srs-form-section name="section1">
                <srs-form-field type="input" value_changed_event="" name="field1" field_data={}/>
                <srs-form-field type="input" name="field2" field_data={}/>
                <srs-form-field type="input" name="field3" field_data={}/>
            </srs-form-section>
            <srs-form-section name="section2">
                ...
            </srs-form-section>
        </srs-form>
*/
const SrsFormField = function (props, ref) {
    const [fieldData, setFieldData] = useState({type: props.type, name: props.name});
    const [fieldComponentRegistered, setFieldComponentRegistered] = useState(false);
    const [eventInitialised, setEventInitialised] = useState(false);
    const getFieldData = (inputFieldData) => {
        let outputFieldData = {
            valueChangeEventName: "",
            tagName: "",
        }; 
        
        if(inputFieldData.data) {
            let fieldKeys = Object.keys(inputFieldData.data);
            fieldKeys.map((fieldKey) => {
                if(typeof inputFieldData.data[fieldKey] == "object") {
                    outputFieldData[fieldKey] = JSON.stringify(inputFieldData.data[fieldKey]);
                }else {
                    outputFieldData[fieldKey] = inputFieldData.data[fieldKey];
                }
            });
        }

        switch(inputFieldData.type) {
            case "input": 
                outputFieldData.valueChangeEventName = EventAPI.INPUT_CHANGED;
                outputFieldData.tagName = "srs-input";
                break;
            case "toggle":
                outputFieldData.valueChangeEventName = EventAPI.TOGGLE_CHANGED;
                outputFieldData.tagName = "srs-toggle";
                break;
            case "dropdown":
                outputFieldData.valueChangeEventName = EventAPI.DROPDOWN_CHANGED;
                outputFieldData.tagName = "srs-dropdown";
                break;
            case "radio":
                outputFieldData.valueChangeEventName = EventAPI.RADIO_CHECKED;
                outputFieldData.tagName = "srs-radiobutton";
                break;
            case "select":
                outputFieldData.valueChangeEventName = EventAPI.SELECT_CHANGED;
                outputFieldData.tagName = "srs-select";
                let selectedItems = inputFieldData.formData && inputFieldData.formData[inputFieldData.fieldName]? inputFieldData.formData[inputFieldData.fieldName].value: inputFieldData.data?.selected || [];
                outputFieldData.selected = JSON.stringify(selectedItems);
                break;
            case "textarea":
                outputFieldData.valueChangeEventName = EventAPI.TEXTAREA_CHANGED;
                outputFieldData.tagName = "srs-textarea";
                break;
            case "datetime":
                outputFieldData.valueChangeEventName = EventAPI.DATETIME_CHANGED;
                outputFieldData.tagName = "srs-datetime";
                break;
            case "time":
                outputFieldData.valueChangeEventName = EventAPI.TIME_CHANGED;
                outputFieldData.tagName = "srs-time";
                break;
            case "typeahead":
                outputFieldData.valueChangeEventName = EventAPI.TYPEAHEAD_CHANGED;
                outputFieldData.tagName = "srs-typeahead";
                break;
            case "checkbox":
                outputFieldData.valueChangeEventName = EventAPI.CHECKBOX_CHANGED;
                outputFieldData.tagName = "srs-checkbox";
                break;
            case "fileupload": 
                outputFieldData.valueChangeEventName = EventAPI.FILE_UPLOADED;
                outputFieldData.tagName = "srs-fileupload";
                break;
            case "contenttree": 
                outputFieldData.tagName = "srs-contenttree";
                break;
            case "codeeditor": 
                outputFieldData.tagName = "srs-codeeditor";
                break;
            default: 
                outputFieldData.valueChangeEventName = inputFieldData.value_changed_event;
                outputFieldData.tagName = inputFieldData.tag_name;
                break;
        }
        return outputFieldData;
    }
    useEffect(() => {
        const currentNode = ref.current;
        if (!currentNode) return;

        let innerFieldData = getFieldData({fieldName: props.name, type: props.type, data: props.data || {}, formData: props.form_data || {}});
        setFieldData(innerFieldData);
        register([innerFieldData.tagName]).then(() => setFieldComponentRegistered(true));

        if(ref.current && props.type && !eventInitialised) { 
            ref.current.addEventListener(innerFieldData.valueChangeEventName, (evt) => {
                let val = evt.detail;
                if(val.payload) {
                    val = val.payload;
                    if(val.hasOwnProperty("value")) {
                        val = val.value;
                    }
                }
                props.dispatchEvent(EventAPI.FORM_FIELD_VALUE_CHANGED, { payload: {value: val, fieldName: props.name, fieldType: props.type}});
            });
            setEventInitialised(true);
        }

        var e = ref.current;
        while(e.nodeType != 11) { // 11 = DOCUMENT_FRAGMENT_NODE
            e = e.parentNode;
        }

        let hostElement = e.host;
        let designtimeWrapper = hostElement.closest(".element-container");
        if(!designtimeWrapper) {
            hostElement.style.flexBasis = `${props.width_fraction*100}%`;
            hostElement.style.padding = "5px";
        }else {
            designtimeWrapper.style.flexBasis = `${props.width_fraction*100}%`;
            designtimeWrapper.style.padding = "5px";
        }

    }, [props.width_fraction, props.type, props.name, props.data, props.form_data]);
    
    let result = null;
    
    if(fieldData.tagName && fieldComponentRegistered) {
        let value = props.form_data && props.form_data[props.name]? props.form_data[props.name].value: props.value;
        result = React.createElement(fieldData.tagName, {
                    ...fieldData,
                    key: props.elementId || props.name, 
                    elementid: props.elementId || props.name,
                    value,
                    ref: el => {
                        if(!el) return;
                        el.context = {...props.context, form: {...props.form_data}};
                        el.clan = [...props.clan];
                    }});
    }

    return result;
}

const styles = `
    
`
const props = [{
    name: "name",
    defaultValue: "",
    type: "string"
}, {
    name: "type",
    defaultValue: "",
    type: "string"
}, {
    name: "width_fraction",
    defaultValue: 0.5,
    type: "number"
}, {
    name: "data",
    defaultValue: {},
    type: "object"
}, {
    name: "form_data",
    defaultValue: {},
    type: "object"
}];

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsFormField), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };